import * as React from "react";
import debounce from "lodash/debounce";
import PropTypes from "prop-types";

import { useHistory, withRouter } from "react-router";
import { connect } from "react-redux";
import { useForm } from "react-hook-form";

import BasicButton from "./BasicButton";
import Icons from "icons";
import HintsNotAvailableMessage from "./HintsNotAvailableMessage";
import HintsSection from "./HintsSection";
import SelectSection from "./SelectSection";

import { fetchImages, fetchHints, clearHints } from "reduxware/redux/imagesReducer";
import { createFormStyleLocationRelative } from "js/common";

function getHovered(node) {
  if (!node) return null;
  const elements = node.querySelectorAll(":hover");
  if (!elements) return null;
  const arr = Array.from(elements);
  return arr[arr.length - 1];
}

const Form = props => {
  const { fetchHints, hints, clearHints, fetchImages } = props;
  const history = useHistory();
  const path = props.match.path;
  const refSelect = React.useRef();
  const foo = e => {
    e.preventDefault();
    return false;
  };

  function getImages(e) {
    if (e.key === "Enter") {
      const withHover = getHovered(refSelect.current);
      if (withHover) {
        const className = withHover.className;
        if (className.includes("option")) {
          fetchImages(withHover.textContent);
          history.push("./images");
        }
      }
    }
  }

  // eslint-disable-next-line react-hooks/exhaustive-deps
  const debouncedGetImages = React.useCallback(
    debounce(e => {
      getImages(e);
    }, 300),
    []
  );
  const { register, getValues, reset } = useForm();

  return (
    <div onKeyDown={debouncedGetImages}>
      <form className={createFormStyleLocationRelative(path, "form")} onSubmit={foo}>
        <BasicButton className="form__button" aria-label="Search">
          <Icons.Search />
        </BasicButton>
        <input
          {...register("pattern")}
          type="text"
          defaultValue=""
          className="form__input"
          onChange={e => {
            register("pattern").onChange(e);
            fetchHints(getValues().pattern);
          }}
          autoCapitalize="none"
          placeholder="Search free high-resolution photos"
        />
        ;
        <BasicButton
          isVisible={Boolean(getValues().pattern || "")}
          className="form__input-clear"
          type="reset"
          onClick={e => {
            reset();
            clearHints();
          }}
        >
          <Icons.Reset />
        </BasicButton>
      </form>
      <div ref={refSelect}>
        <SelectSection hints={hints} getValues={getValues} fetchImages={fetchImages} />
      </div>
      <HintsSection path={path} hints={hints} />
      <HintsNotAvailableMessage />
    </div>
  );
};

const mapDispatchToProps = dispatch => ({
  fetchHints: str => dispatch(fetchHints(str)),
  clearHints: str => dispatch(clearHints(str)),
  fetchImages: str => dispatch(fetchImages(str)),
});

const mapStateToProps = state => ({
  hints: state.images.hints,
});

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Form));

Form.propTypes = {
  fetchHints: PropTypes.func,
  fetchImages: PropTypes.func,
  clearHints: PropTypes.func,
  hints: PropTypes.array,
};
