import * as React from "react";

import { connect } from "react-redux";
import { RouteComponentProps, withRouter } from "react-router";

import { renderWhenIsVisible } from "HOCs";
import { RootStateType } from "../AppProvider";

const HintsNotAvailable = (props: RouteComponentProps) => {
  const path = props.match.path;
  const classy = path === "/images" ? "noHints--short search-text--dark" : "noHints search-text--dark";

  return <h2 className={classy}>Nie znaleziono podpowiedzi dla wprowadzonego tekstu</h2>;
};

const mapStateToProps = (state: RootStateType) => ({
  isVisible: state.images.hintsMessageVisible,
});

export default connect(mapStateToProps)(renderWhenIsVisible(withRouter(HintsNotAvailable)));
