import * as React from "react";
import ImagesHint from "./ImagesHint";
import { hints } from "./model";
import { RouteComponentProps, withRouter } from "react-router";

interface Props {
  hints: hints;
}
const HintsSection = (props: Props & RouteComponentProps) => {
  const { hints } = props;
  const path = props.match.path;
  return hints?.length && path ? (
    <section className="images-hints-wrapper" id="images-hints-wrapper">
      {hints.map((hint, index) => (
        <ImagesHint key={index} hint={hint} />
      ))}
    </section>
  ) : null;
};

export default withRouter(HintsSection);
