import * as React from "react";
import { hint } from "./model";

interface Props {
  hint: hint;
}

const ImagesHint = (props: Props) => {
  const { hint } = props;
  return hint && hint.label && hint.value ? (
    <div data-image_hint={hint.value} className="images-hint">
      {hint.label}
    </div>
  ) : null;
};
export default ImagesHint;
