import * as React from "react";
import Select from "react-select";
import { hints } from "./model";
import { useHistory, withRouter, RouteComponentProps } from "react-router";

interface Props {
  hints: hints;
  getValues: any;
  fetchImages: Function;
}

const SelectSection = (props: Props & RouteComponentProps) => {
  const { hints, getValues, fetchImages } = props;
  const history = useHistory();
  const path = props.match.path;

  return hints?.length && path === "/" ? (
    <Select
      className="select-top"
      id="BigSelect"
      value={getValues}
      isClearable={true}
      menuIsOpen={true}
      onChange={selectValue => {
        fetchImages(selectValue.value);
        history.push("./images");
      }}
      options={hints}
    />
  ) : null;
};

export default withRouter(SelectSection);
